.home ul {
  position: -webkit-sticky;
  position: sticky;
  top: 16px;
  z-index: 15; }
.home ul .menu-bar {
  display: none; }
@media (max-width: 991px) {
  .home ul .menu-bar {
    display: inline-block;
    background-color: #000;
    color: #da8516; } }
@media (max-width: 991px) {
  .home ul {
    background-color: #000; } }
.home .nav-bar ul li {
  display: inline-block;
  font-size: 20px;
  font-weight: 600;
  background-color: transparent; }
.home .nav-bar ul li a {
  position: relative; }
.home .nav-bar ul li a::before {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  margin: auto;
  background-color: #000;
  width: 0;
  height: 2px;
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
  -moz-transition: 0.3s ease;
  -ms-transition: 0.3s ease;
  -o-transition: 0.3s ease; }
.home .nav-bar ul li a:hover {
  color: #da8516; }
.home .nav-bar ul li a:hover::before {
  width: 100%; }
.home .nav-bar ul li a.active {
  color: #da8516; }
.home .nav-bar ul li a.active::before {
  width: 100%; }
@media (max-width: 991px) {
  .home .nav-bar {
    display: none; } }
.home .information {
  z-index: 99;
  position: relative; }
@media (max-width: 991px) {
  .home .img-order {
    order: -1;
    text-align: center;
    margin-bottom: 30px; } }
.home a {
  text-decoration: none;
  color: #000; }
.home .side-nav {
  z-index: 9; }
@media (max-width: 991px) {
  .home .side-nav {
    order: -1;
    width: 100vw;
    padding: 0;
    margin: 0;
    z-index: 99; } }
.home .ar {
